<template>
	<div style="display: flex; flex-direction: column; height: 100%;">

		<div style=" width: 100%; display: flex; flex-direction: row; margin-bottom: 10px; align-items: center; box-sizing:
				border-box;">
			<h1 style="margin: 0px; color: white;">{{info.tid}},
				{{info.name}}
			</h1>
			<div style="flex:1"></div>
			<el-button type="danger" size="medium" icon="el-icon-top-left" round @click="logout()">
			</el-button>
		</div>

		<div
			style="background-color: white; flex:1; position: fixed; top: 140px; left: 280px; right: 200px; bottom: 80px; border-radius: 10px; box-shadow: rgb(0 0 0 / 7%) 0px 5px 9px 9px; overflow: hidden; display: flex; flex-direction: row;">

			<transition name="slide-fade2">
				<div v-if="page==='main'"
					style="margin: 12px; position: absolute; top: 0; right: 0; bottom: 0; left: 0; flex: 1; overflow: hidden;"
					tabPosition="left" @tab-click="on_Tab_changed($event.label)">
					<div style="display: flex; flex-direction: column; gap: 12px; overflow: visible; height: 100%;">
						<el-select style="align-self: flex-end; margin: 10px 10px 0px 0px;" v-model="selectedTerm"
							placeholder="" @change="switch_Term($event)">
							<el-option v-for="item in termList" :key="item.term" :label="item.term" :value="item.term">
							</el-option>
						</el-select>
						<div
							style="display: flex; flex-direction: row; gap: 20px; box-sizing: content-box; ; flex-wrap: wrap; overflow: auto; overflow: auto; padding: 10px; flex: 1; align-content:flex-start;">
							<template v-for="item in courseList">
								<div class="coursecard" :key="item.cid"
									style="flex:1; display: flex; flex-direction: column; padding: 10px 15px"
									:style="{'background': `linear-gradient(45deg, ${color[item.id**3 % color.length]}, ${color[(item.id**3+1) % color.length]})`}">

									<div class="container"
										style="display: flex; flex-direction: row; justify-content: space-between; flex: 1; gap: 20px;">
										<div class="vcontainer">

											<a
												style="color: white; font-size: 24px; font-weight: bolder; word-break: keep-all;">{{item.name}}
												<span style="font-weight: lighter;">{{item.cid}}</span></a>
											<div class="container">
												<div class="vcontainer">
													<a style="color: white; font-size: 16px; font-weight: bolder;">{{item.tname}}
														<span style="font-weight: lighter;">{{item.tid}}</span></a>
													<a style="color: white; font-size: 16px; font-weight: lighter;">{{item.credit}}
														<span style="font-weight: bolder;"></span></a>
												</div>
											</div>
										</div>
										<div class="container"
											style="justify-content: center; flex: unset; flex-direction: column; gap: 13px; align-items: flex-end; padding-top: 5px; justify-content: space-between;">
											<div
												style="width:min-content; background-color: white; padding: 5px 12px 5px 11px; border-radius: 5px; height: min-content;">
												<a style="font-weight: bolder; font-family: consolas;"
													:style="{'font-size': '30px', 'font-style': 'italic', 'color': color[(item.id**3+1) % color.length]}">{{Math.round(item.avg*100)/100}}</a>
											</div>
											<div style="display: flex; flex-direction: row; gap: 10px;">
												<el-button size="small" icon="el-icon-more" circle
													style="margin: none;align-self: flex-end;">
												</el-button>
												<el-button style="align-self: flex-end;" type="success" size="small"
													icon="el-icon-plus" round @click="gradeinput(item)">
													
												</el-button>

											</div>

										</div>
									</div>
								</div>
							</template>
						</div>
						<!-- <div
								style="flex: 1; display: flex; flex-direction: column; align-items: center;justify-content: center;">
								<canvas sty id="chart"></canvas>
							</div> -->
					</div>
					<!-- <el-tab-pane label="">
						<div
							style="display: flex; flex-direction: column; gap: 20px; overflow: visible; padding: 0px 0px 0px 10px; align-items: center;">
							<el-select style="align-self: flex-end;" v-model="selectedCourse" placeholder=""
								@change="switch_Course($event)">
								<el-option v-for="item in get_CourseOptions" :key="item.value" :label="item.label"
									:value="item.value">
								</el-option>
							</el-select>
							<div style="height: 600px; width: 100%;">
								<el-table height="600" :data="studentList" style="width: 100%">
									<el-table-column prop="sid" label="">
									</el-table-column>
									<el-table-column prop="name" label="">
									</el-table-column>
									<el-table-column label="" width="140">
										<template slot-scope="scope">
											<el-input type="number" min="0" max="100" step="1"
												v-model.number="scope.row.grade">
											</el-input>
										</template>
									</el-table-column>
								</el-table>
							</div>
							<div
								style="display: flex; margin-top: 20px; flex-direction: row; align-items: center; justify-content: center;">
								<el-button type="primary" @click="update_Grade()" round> </el-button>
							</div>
						</div>
					</el-tab-pane> -->
					<el-tab-pane label="">
						<div
							style="display: flex; flex-direction: column; gap: 20px; overflow: visible; padding: 0px 0px 0px 10px; align-items: center;">
							<el-button style="align-self: flex-end;" type="success" size="medium" icon="el-icon-plus"
								round @click="applyDialog = true">
								
							</el-button>
							<div style="flex:1;width: 100%;">
								<el-table :data="applyCourseList" style="flex:1;width: 100%">
									<el-table-column prop="cid" label="">
									</el-table-column>
									<el-table-column prop="name" label="">
									</el-table-column>
									<el-table-column prop="term" label="">
									</el-table-column>
									<el-table-column prop="credit" label="">
									</el-table-column>
									<el-table-column label="" width="70">
										<template slot-scope="scope">
											<el-button @click="delete_Application(scope.row)" type="danger" size="small"
												icon="el-icon-delete" circle></el-button>
										</template>
									</el-table-column>
								</el-table>
							</div>
						</div>
					</el-tab-pane>
					<el-tab-pane label="">
						<div style="padding: 0px 200px 0px 210px;">
							<el-form :model="passwordForm">
								<el-form-item prop="old" label="">
									<el-input v-model="passwordForm.old"></el-input>
								</el-form-item>
								<el-form-item prop="new" label="">
									<el-input v-model="passwordForm.new"></el-input>
								</el-form-item>
							</el-form>
							<div
								style="display: flex; margin-top: 20px; flex-direction: row; align-items: center; justify-content: center;">
								<el-button type="primary" @click="set_Password()" round>   </el-button>
							</div>
						</div>
					</el-tab-pane>
				</div>

				<!-- </el-card> -->
			</transition>
			<transition name="slide-fade">
				<teacher v-if="page==='gradeinput'"
					style="margin: 22px; position: relative; top: 0px; flex: 1; position: absolute;  top: 0; right: 0; bottom: 0; left: 0;"
					:colors="{color1: gradeinput_course.color1, color2: gradeinput_course.color2}"
					:id="gradeinput_course.id" :courseinfo="gradeinput_course.info" @back="page = 'main'">
				</teacher>
			</transition>
		</div>

		<div
			style="position: fixed; left: 180px; width: 80px; top: 150px; display: flex; flex-direction: column; gap: 10px;">
			<div class="tabs" :class="{'selected': currentTab==='courses'}" @click="currentTab ='courses'">
				<a style="margin: auto; line-height: 12px;"></a>
			</div>
			<div class="tabs" :class="{'selected': currentTab==='apply'}" @click="currentTab ='apply'">
				<a style="margin: auto; line-height: 12px;"></a>
			</div>
			<div class="tabs" :class="{'selected': currentTab==='profile'}" @click="currentTab ='profile'">
				<a style="margin: auto; line-height: 12px;"></a>
			</div>
		</div>

		<el-dialog :visible.sync="applyDialog" width="400px">
			<el-form :model=" courseForm" :rules="applyDialogRules" ref="applyForm" class="demo-ruleForm">
				<el-form-item label="" prop="cid">
					<el-input v-model="courseForm.cid"></el-input>
				</el-form-item>
				<el-form-item label="" prop="name">
					<el-input v-model="courseForm.name"></el-input>
				</el-form-item>
				<el-form-item label="" prop="credit">
					<el-input-number v-model="courseForm.credit" :min="0" :max="10"></el-input-number>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="applyDialog = false" round> </el-button>
				<el-button type="primary" @click="apply_Course()" round> </el-button>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	import Chart from 'chart.js'
	import T from './../components/TeacherHubGradeInput.vue'

	export default {
		name: 'teacherhub',
		components: {
			teacher: T
		},
		data() {
			return {
				page: 'main',
				currentTab: 'courses',
				gradeinput_course: {
					color1: "red",
					color2: "red",
					id: -1
				},

				applyDialog: false,
				courseForm: {
					cid: '',
					name: '',
					credit: 1
				},
				applyDialogRules: {
					cid: [
						{ required: true, message: '', trigger: 'blur' },
						{ min: 1, max: 50, message: ' 3  50 ', trigger: 'blur' }
					],
					name: [
						{ required: true, message: '', trigger: 'blur' },
						{ min: 1, max: 50, message: ' 3  50 ', trigger: 'blur' }
					]
				},
				passwordForm: {
					old: '',
					new: ''
				},
				info: { name: '', id: '' },
				selectedCourse: undefined,
				selectedTerm: undefined,
				courseList: [],
				termList: [],
				studentList: [],
				applyCourseList: [],
				studentListOld: [],
				color: ['#9b1645', '#e91e63', '#9c27b0', '#673ab7', '#3f51b5', '#2196f3', '#00bcd4', '#009688', '#59662c', '#9fa328', '#ffdd00', '#ff9800', '#ff5722'],
				chartdata: {
					labels: [],
					datasets: [{
						label: '',
						data: [],
						backgroundColor: [
							'#D32F2F', '#7B1FA2', '#303F9F', '#1976D2', '#689F38', '#FBC02D', '#FFA000', '#F57C00', '#E64A19', '#5D4037', '#111d5e', '#2f2519', '#c70039', '#184d47', '#f37121', '#ffbd69', '#b83b5e', '#3ec1d3', '#6c5b7b', '#455d7a', '#8f8787'
						],
						borderColor: [
							'#D32F2F', '#7B1FA2', '#303F9F', '#1976D2', '#689F38', '#FBC02D', '#FFA000', '#F57C00', '#E64A19', '#5D4037', '#111d5e', '#2f2519', '#c70039', '#184d47', '#f37121', '#ffbd69', '#b83b5e', '#3ec1d3', '#6c5b7b', '#455d7a', '#8f8787'
						],
						borderWidth: 2
					}]
				}
			}
		},
		computed: {
			get_CourseOptions: function () {
				return this.courseList.map((i) => {
					return { label: `${i.cid} ${i.name}`, value: i.id }
				})
			}
		},
		methods: {
			get_Course(id, term) {
				let param = { id: id, term: term }
				return this.$axios.post("http://127.0.0.1:8000/teacher/getCourse/", param)
					.then(res => {
						// console.log(res.data)
						if (res.data.state === 'failed') {
							this.$message.error({
								message: res.data.data,
								showClose: true
							});
						}
						else {
							this.courseList = res.data.list
							// console.log(res.data)
							let label = []
							let data = []
							let color = []
							this.courseList.forEach(i => {
								label.push(i.name)
								data.push(i.avg)
								color.push(this.color[i.id % this.color.length])
							})
							this.chartdata.labels = label
							this.chartdata.datasets[0].data = data
							this.chartdata.datasets[0].borderColor = color
							this.chartdata.datasets[0].backgroundColor = color
							this.canvas.update()
						}
					})
			},
			logout() {
				localStorage.login = JSON.stringify({})
				this.$router.go(-1)
			},
			set_Password() {
				this.$axios.post("http://127.0.0.1:8000/teacher/setPassword/", { id: this.info.id, old: this.passwordForm.old, new: this.passwordForm.new })
					.then(res => {
						if (res.data.state === 'failed') {
							this.$message.error({
								message: res.data.data,
								showClose: true
							});
						}
						else {
							this.$message.success({
								message: res.data.data,
								showClose: true
							});
							this.logout()
						}
					})
			},
			on_Tab_changed(tab) {
				if (tab === '') {
					this.$axios.post("http://127.0.0.1:8000/teacher/getCourseTerm/", { id: this.info.id })
						.then(res => {
							console.log(res.data)
							if (res.data.state === 'failed') {
								this.$message.error({
									message: res.data.data,
									showClose: true
								});
								return false
							}
							else {
								this.termList = res.data.list
								return true
							}
						}).then((res) => {
							if (res) {
								if (this.selectedTerm === undefined) {
									this.selectedTerm = this.termList[0].term
								}
								this.get_Course(this.info.id, this.selectedTerm)
							}
						})


				}
				else if (tab === '' && this.selectedCourse !== undefined) {
					this.switch_Course(this.selectedCourse)
				}
				else if (tab === '') {
					this.$axios.post("http://127.0.0.1:8000/teacher/getApplyCourse/", { id: this.info.id })
						.then(res => {
							// console.log(res.data)
							if (res.data.state === 'failed') {
								this.$message.error({
									message: res.data.data,
									showClose: true
								});
							}
							else {
								this.applyCourseList = res.data.list
							}
						})
				}
			},
			switch_Course(id) {
				this.$axios.post("http://127.0.0.1:8000/teacher/getCourseGrade/", { id: id })
					.then(res => {
						// console.log(res.data)
						if (res.data.state === 'failed') {
							this.$message.error({
								message: res.data.data,
								showClose: true
							});
						}
						else {
							this.studentList = res.data.list
							this.studentListOld = res.data.list.map((i) => { return i.grade })
						}
					})
			},
			gradeinput(item) {
				// console.log(item)
				this.gradeinput_course = {
					color1: this.color[(item.id ** 3) % this.color.length],
					color2: this.color[(item.id ** 3 + 1) % this.color.length],
					id: item.id,
					info: item
				}
				this.page = 'gradeinput'
			},
			switch_Term() {
				this.get_Course(this.info.id, this.selectedTerm)
			},
			update_Grade() {
				let list = this.studentList.filter((i, idx) => {
					return i.grade !== this.studentListOld[idx]
				}).map((i) => {
					return { id: i.euid, grade: Math.max(Math.min(100, i.grade)) }
				})
				console.log(list)
				if (list.length <= 0) return
				this.$axios.post("http://127.0.0.1:8000/teacher/updateGrade/", { cuid: this.selectedCourse, list: list })
					.then(res => {
						// console.log(res.data)
						if (res.data.state === 'failed') {
							this.$message.error({
								message: res.data.data,
								showClose: true
							});
						}
						else {
							this.$message.success({
								message: '',
								showClose: true
							});
						}
					})
			},
			apply_Course() {
				let data = {
					cid: this.courseForm.cid,
					name: this.courseForm.name,
					credit: this.courseForm.credit,
					tid: this.info.id
				}
				this.$refs.applyForm.validate((valid) => {
					if (valid) {
						this.$axios.post("http://127.0.0.1:8000/teacher/applyCourse/", data)
							.then(res => {
								if (res.data.state === 'failed') {
									this.$message.error({
										message: res.data.data,
										showClose: true
									});
								}
								else {
									this.$message.success({
										message: res.data.data,
										showClose: true
									});
									this.reset_Form("applyForm")
									this.applyDialog = false;
									this.on_Tab_changed('')
								}
							})
					} else {
						this.$message.error({
							message: "",
							showClose: true
						})
					}
				});
			},
			delete_Application(row) {
				this.$axios.post("http://127.0.0.1:8000/teacher/deletApplyCourse/", { id: row.id })
					.then(res => {
						if (res.data.state === 'failed') {
							this.$message.error({
								message: res.data.data,
								showClose: true
							});
						}
						else {
							this.$message.success({
								message: res.data.data,
								showClose: true
							});
							this.on_Tab_changed('')
						}
					})

			},
			reset_Form(formName) {
				this.$refs[formName].resetFields();
			}
		},
		mounted() {
			this.info = JSON.parse(localStorage.login)
			this.on_Tab_changed('')
			let ctx = document.getElementById('chart')
			this.canvas = new Chart(ctx, {
				type: 'bar',
				data: this.chartdata,
				options: {
					scales: {
						yAxes: [{
							ticks: {
								beginAtZero: true
							}
						}]
					}
				}
			})
		}
	}
</script>

<style>
	.slide-fade-enter-active {
		transition: all .2s ease-out;
	}

	.slide-fade-leave-active {
		transition: all .2s ease-out;
	}

	.slide-fade-enter {
		transform: translateX(100%);
		opacity: 0.5;
	}

	.slide-fade-leave-to {
		transform: translateX(100%);
		opacity: 0.5;
	}

	.slide-fade2-enter-active {
		transition: all .2s ease-out;
	}

	.slide-fade2-leave-active {
		transition: all .2s ease-out;
	}

	.slide-fade2-enter {
		transform: translateX(-100%);
		opacity: 0.5;
	}

	.slide-fade2-leave-to {
		transform: translateX(-100%);
		opacity: 0.5;
	}

	.el-transfer-panel {
		width: 350px;
		height: 350px;
		margin-bottom: 20px;
	}

	.container {
		display: flex;
		flex-direction: row;
		flex: 1;
	}

	.vcontainer {
		display: flex;
		flex-direction: column;
		flex: 1;
		gap: 4px;
	}

	.tabs {
		padding: 12px 0px;
		border-radius: 6px;
		display: flex;
		flex-direction: column;
		align-content: center;
		color: rgb(63, 107, 226);
		box-shadow: rgba(63, 106, 226, 0.26) 0px 5px 7px 0px;
		background-color: rgb(255, 255, 255);
		transition: all .2s ease-out;
	}

	.tabs.selected {
		background-color: rgb(63, 107, 226);
		box-shadow: #00000026 0px 5px 7px 0px;
		color: white;
		transition: all .2s ease-out;
	}

	.coursecard {
		border-radius: 10px;
		box-shadow: #00000026 0px 5px 7px 0px;
		transform: scale(1) translateX(0px);
		transition: all .2s ease-out;
	}

	.coursecard:hover {
		border-radius: 10px;
		box-shadow: #00000041 0px 10px 12px 0px;
		transform: scale(1.01) translateY(-2px);
		transition: all .2s ease-out;
	}

	.el-tabs__header.is-left {
		position: sticky;
		top: 0;
	}
</style>